.contacts {
    min-height: 100vh;
    background-color: #1f1f20;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.container {
    width: 80vw;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.title {
    height: 10%;
    margin: 10px;
    padding: 10px;
}

.container form {
    display: flex;
    flex-direction: column;
    width: 60%;
}


.contactForm input,
.contactForm textarea {
    background: #252527 !important;
    border: 1px solid #4a4a4a;
    font-size: 12px;
    padding: 10px 15px;
    border-radius: 0;
    margin-bottom: 15px;
    outline: none;
    -webkit-transition: all .4s ease-in-out 0s;
    transition: all .4s ease-in-out 0s;
    color: #495057;
    min-height: 10px;
}

.contactForm textarea {
    height: 100px;
}

